:root {
  --bg: #f2f3f5; /* #f1eeee;  cream white (updated) */
  --text: #222222; /* charcoal black */
  --accent: #3b82f6; /* soft blue */
  --highlight: #ffd166; /* warm yellow */
  --muted: #6b6b6b;
}

* {
  box-sizing: border-box;
}
html,
body,
#root {
  height: 100%;
}
body {
  margin: 0;
  /* hard fallback first, then the CSS variable to ensure exact color */
  background: #f2f3f5;
  background: var(--bg);
  color: var(--text);
  font-family: "Inter", system-ui, -apple-system, "Segoe UI", Roboto,
    "Helvetica Neue", Arial;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1.5;
  scroll-behavior: smooth;
}

h1,
h2,
h3 {
  font-family: "Poppins", "Montserrat", sans-serif;
  color: var(--text);
  margin: 0;
}
p {
  color: var(--muted);
  margin: 0;
}

.container {
  max-width: 1100px;
  margin: 0 auto;
  padding: 0 1rem;
}

/* Colors section base spacing */
.colors-section p {
  max-width: 640px;
}

/* Buttons */
.btn {
  display: inline-flex;
  align-items: center;
  gap: 0.6rem;
  border-radius: 10px;
  font-weight: 600;
  cursor: pointer;
  border: none;
  /* ensure anchor buttons don't show the browser's default underline */
  text-decoration: none;
}
.btn:hover,
.btn:focus,
.btn:active {
  text-decoration: none;
}
.btn-primary {
  background: var(--accent);
  color: white;
  padding: 0.65rem 1rem;
  box-shadow: 0 6px 20px rgba(59, 130, 246, 0.18);
}
.btn-ghost {
  background: var(--highlight);
  color: #111;
  padding: 0.6rem 0.9rem;
  box-shadow: 0 6px 18px rgba(255, 209, 102, 0.14);
}

/* Smooth fade-in on scroll: default hidden, reveal when .in-view is added */
.fade-up {
  opacity: 0;
  transform: translateY(8px);
  transition: opacity 700ms ease, transform 700ms ease;
}
.fade-up.in-view {
  opacity: 1;
  transform: none;
}

/* Card common */
.card {
  background: white;
  border-radius: 14px;
  box-shadow: 0 8px 30px rgba(16, 24, 40, 0.06);
  padding: 1rem;
}

/* layout helpers previously inline */
.main-content {
  padding-top: 32px;
}
.section {
  margin-top: 28px;
}

/* Ensure in-page anchor targets appear below the sticky navbar on desktop.
   Use scroll-margin-top so fragment navigation and scrollIntoView place the
   section's top just under the navbar. On small screens the navbar is taller
   or different, so we only apply this on wider viewports. */
@media (min-width: 720px) {
  .section,
  section[id] {
    scroll-margin-top: calc(var(--nav-min-height, 72px) + 8px);
  }
}
.section p {
  margin-top: 8px;
}
.section-bottom {
  margin-bottom: 80px;
}

/* Accessibility helper */
.sr-only {
  position: absolute !important;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

/* Contact section */
.contact-section {
  padding: 48px 0 80px;
}
.contact-grid {
  display: grid;
  grid-template-columns: 1fr 380px;
  gap: 28px;
  align-items: start;
}
.contact-form input,
.contact-form textarea {
  width: 100%;
  padding: 12px 14px;
  border-radius: 8px;
  /* use the site's accent color (soft blue) at low opacity for a subtle accent border */
  border: 1px solid rgba(59, 130, 246, 0.12);
  margin-top: 12px;
  font-size: 15px;
}
.contact-form textarea {
  resize: vertical;
}
.contact-actions {
  margin-top: 16px;
  display: flex;
  gap: 12px;
}
.contact-info .contact-list {
  margin-top: 12px;
  padding-left: 0;
  list-style: none;
}
.contact-info .contact-list li {
  margin-top: 8px;
  color: var(--muted);
}
.socials {
  margin-top: 16px;
  display: flex;
  gap: 10px;
}
.social-link {
  color: var(--accent);
  font-weight: 600;
  text-decoration: none;
}

@media (max-width: 900px) {
  .contact-grid {
    grid-template-columns: 1fr;
  }
}

/* Faster fade-in on mobile to reduce perceived lag while scrolling */
@media (max-width: 640px) {
  .fade-up {
    transition: opacity 320ms ease, transform 320ms ease;
  }
}

/* About section layout */
.about-section {
  padding: 18px 0 36px;
}

/* Pull About section upward to sit closer under the hero */
.about-section {
  margin-top: -40px;
}
.about-grid {
  display: flex;
  align-items: flex-start;
  gap: 28px;
}
/* visually contain the About content with a rounded card-like border */
.about-grid {
  background: rgba(255, 255, 255, 0.98);
  padding: 22px;
  border-radius: 14px;
  border: 1px solid rgba(16, 24, 40, 0.06);
  box-shadow: 0 8px 30px rgba(16, 24, 40, 0.04);
}
.about-left,
.about-right {
  flex: 1 1 0;
}
.about-center {
  flex: 0 0 360px; /* fixed center column */
}
.about-left h2 {
  font-size: 36px;
  margin-bottom: 12px;
}

/* Heading at the section level for the About area */
.about-section > h2 {
  font-size: 36px;
  margin-bottom: 18px;
}
.about-left p {
  max-width: 460px;
}
.about-center .stat-card {
  padding: 18px;
  /* make the middle card stand out with a soft light-red background */
  background: #ffecec; /* very light red */
  border: 1px solid rgba(255, 100, 100, 0.12);
}
.stat-top {
  display: flex;
  align-items: center;
  gap: 18px;
}
.stat-num {
  font-size: 52px;
  font-weight: 800;
  line-height: 1;
}
.stat-label {
  font-size: 14px;
  color: var(--muted);
}
.stat-desc {
  margin-top: 12px;
  color: var(--muted);
}
.stat-image img {
  width: 100%;
  height: 220px;
  object-fit: cover;
  border-radius: 10px;
}
.about-right {
  display: flex;
  flex-direction: column;
  gap: 18px;
}
.mini-avatar {
  width: 120px;
  height: 120px;
  padding: 0;
  overflow: hidden;
}
.about-bullets {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 12px;
}
.about-bullets li {
  display: flex;
  gap: 12px;
  align-items: flex-start;
  color: var(--muted);
}

@media (max-width: 900px) {
  .about-grid {
    flex-direction: column;
    text-align: center;
    align-items: center;
  }
  .about-section {
    margin-top: -20px; /* smaller pull-up on narrow screens */
  }
  .about-right {
    align-items: center;
  }
  .mini-avatar {
    width: 160px;
    height: 160px;
    display: none;
  }
  /* .mini-av-img {
    display: none;
  } */
  .stat-image img {
    height: 160px;
  }
  .about-center {
    flex: none;
    width: 100%;
    max-width: 420px;
  }
  .about-left,
  .about-right {
    width: 100%;
  }
}
